import React, {useState} from 'react';
import {
    Gallery,
    Header,
    Placeholder,
    Banner,
    Panel,
    PanelHeader,
    Card,
    CardGrid,
    PanelHeaderBack,
    Button, MiniInfoCell, Caption, Subhead, TabsItem, Tabs, Div, HorizontalScroll
} from "@vkontakte/vkui";
import './Home.css'
import Icon16Chevron from '@vkontakte/icons/dist/16/chevron';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';

const cart = [
    {id: 1, name: 'Корм сухой для ежей "Spike’s Dinner"', img: 'https://zoomagasin.ru/upload/iblock/e85/e855157d9531638cd72ddaae0528dcb8.jpg', price: 810},
    {id: 2, name: 'Корм сухой для ежей "Spike’s Dinner"', img: 'https://zoomagasin.ru/upload/iblock/e85/e855157d9531638cd72ddaae0528dcb8.jpg', price: 810},
    {id: 3, name: 'Корм сухой для ежей "Spike’s Dinner"', img: 'https://zoomagasin.ru/upload/iblock/e85/e855157d9531638cd72ddaae0528dcb8.jpg', price: 810},
    {id: 4, name: 'Корм сухой для ежей "Spike’s Dinner"', img: 'https://zoomagasin.ru/upload/iblock/e85/e855157d9531638cd72ddaae0528dcb8.jpg', price: 810},
    {id: 5, name: 'Корм сухой для ежей "Spike’s Dinner"', img: 'https://zoomagasin.ru/upload/iblock/e85/e855157d9531638cd72ddaae0528dcb8.jpg', price: 810}
];
const Category = ({ id, go, fetchedUser, targetCategory }) => {
    const [subcat, setSubcat] = useState(targetCategory.subcat);
    const [activeSubcat, setActiveSubcat] = useState(null);
    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
            <Placeholder
                icon={<div style={{
                    background: 'url('+targetCategory.icon+') no-repeat',
                    height: 50,
                    width: 50
                }}/>}
            >
                Выбрана категория: {targetCategory.title}
            </Placeholder>
            {
                targetCategory.subcat &&
                <HorizontalScroll>
                    <Tabs>
                        {
                            targetCategory.subcat.map(subcat=>{
                                return (
                                    <TabsItem
                                        onClick={() => {
                                            setActiveSubcat(subcat)
                                        }}
                                        selected={activeSubcat === subcat}
                                    >
                                        {subcat}
                                    </TabsItem>
                                )
                            })
                        }
                    </Tabs>
                </HorizontalScroll>
            }
            {
                activeSubcat !== null &&
                <MiniInfoCell textWrap={'full'}>Ниже будут выводиться карточки с - {activeSubcat}</MiniInfoCell>
            }
            <Div>
                <CardGrid>
                    {
                        cart.map(item=>{
                            return(
                                <Card
                                    style={{padding: 2, borderRadius: 13, margin: 0, backgroundColor: '#007151'}}
                                    size="m"
                                    mode="shadow"
                                >
                                    <div style={{
                                        height: 150,
                                        backgroundImage: 'url(' + item.img + ')',
                                        backgroundSize: 'cover',
                                        backgroundPosition: 'center 35%',
                                        backgroundRepeat: 'no-repeat',
                                        borderRadius: 13
                                    }}>
                                    </div>
                                    <MiniInfoCell textWrap={'full'}><Caption level={1}>{item.name}</Caption></MiniInfoCell>
                                    <MiniInfoCell before={<Icon24MoneyCircle height={20} width={20}/>}><Subhead>{item.price} руб.</Subhead></MiniInfoCell>
                                    <MiniInfoCell><Button size="m" stretched mode="outline">Купить</Button></MiniInfoCell>
                                </Card>
                            )
                        })
                    }
                </CardGrid>
            </Div>
        </Panel>
    )
};

export default Category;
